import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import Navbar from "./components/navbar";
import BodyContent from "./components/bodyContent";
import Header from "./components/header";

const blogName = "Blog Name To Be Determined";
const authorName = "Ayden Diel";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
    };
  }

  componentDidMount() {
    fetch("/api")
      .then((res) => res.json())
      .then((data) => {
        this.setState({ data: data });
      });
  }

  render() {
    return (
      <div>
        <Header blogName={blogName} authorName={authorName} />
        <Navbar currentMenuIndex="0" menuItems={["Blog", "Artwork", "About"]} />
        {this.state.data ? (
          <BodyContent articles={this.state.data.articles} />
        ) : (
          <p>Loading...</p>
        )}
      </div>
    );
  }
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById("root")
);

