import React from "react";

class Navbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      menuItems: props.menuItems,
      currentMenuIndex: props.currentMenuIndex,
    };
    this.menuItemElements = this.state.menuItems.map((name, index) => {
      return (
        <NavbarItem
          name={name}
          selected={this.state.currentMenuIndex == index}
          key={index}
        />
      );
    });
  }
  render() {
    return (
      <div id="navbar">
        <h3>
          {this.menuItemElements}
        </h3>
      </div>
    );
  }
}

function NavbarItem(props){
  return !props.selected ? (
    <a className="menuItem" href="#">
      {props.name + " "}
    </a>
  ) : (
    <a className="menuItem">{props.name + " "}</a>
  );
}

